import React, { useEffect, useState } from "react";
import Filter from "./components/Filter";
import PersonForm from "./components/PersonForm";
import Persons from "./components/Persons";
import personService from "./services/phonebook";
import { Grid, makeStyles } from "@material-ui/core";
import Notification from "./components/Notification";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.primary.main,
    height: "100vh",
  },
}));

const App = () => {
  const classes = useStyles();
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [newFilter, setNewFilter] = useState("");
  const [notificationMessage, setNotificationMessage] = useState(null);
  const [notificationStatus, setNotificationStatus] = useState(null);

  useEffect(() => {
    getAllPersons();
  }, []);

  const getAllPersons = () => {
    personService.getAll().then((people) => setPersons(people));
  };

  const resetForm = () => {
    setNewName("");
    setNewNumber("");
  };

  const setNotification = (message, status) => {
    setNotificationMessage(message);
    setNotificationStatus(status);
    setTimeout(() => {
      setNotificationMessage(null);
    }, 5000);
  };

  const createPerson = (newPerson) => {
    personService
      .create(newPerson)
      .then((addedPerson) => {
        setNotification(`Added ${addedPerson.name}`, "success");
        setPersons(persons.concat(addedPerson));
        resetForm();
      })
      .catch((error) => {
        setNotification(error.response.data.error, "error");
      });
  };

  const updatePerson = (id, newPersonObject) => {
    personService
      .update(id, newPersonObject)
      .then((updatedPerson) => {
        setNotification(`Updated ${updatedPerson.name}`, "success");
        setPersons(
          persons.map((person) => (person.id !== id ? person : updatedPerson))
        );
        resetForm();
      })
      .catch((error) => {
        setNotification(error.response.data.error, "error");
      });
  };

  const addPerson = (event) => {
    event.preventDefault();
    const newPerson = { name: newName, number: newNumber };
    const duplicateName = persons.some(
      (person) => person.name.toLowerCase() === newName.toLowerCase()
    );
    if (duplicateName) {
      if (
        window.confirm(
          `${newPerson.name} is already in the phonebook, replace the old number with a new one?`
        )
      ) {
        const personId = persons.find(
          (person) => person.name.toLowerCase() === newName.toLowerCase()
        ).id;
        updatePerson(personId, newPerson);
      }
      return;
    }
    createPerson(newPerson);
  };

  const handleDeleteClick = (person) => {
    if (!window.confirm(`Delete ${person.name}?`)) return;
    personService.remove(person.id).then(() => {
      setPersons(persons.filter((people) => people.id !== person.id));
    });
  };

  const handleNameChange = (event) => setNewName(event.target.value);
  const handleNumberChange = (event) => setNewNumber(event.target.value);
  const handleFilterChange = (event) => setNewFilter(event.target.value);

  return (
    <div className={classes.root}>
      <Grid
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
      >
        <h1>Phonebook</h1>
        <Notification
          message={notificationMessage}
          status={notificationStatus}
        />
        <Filter newFilter={newFilter} handleFilterChange={handleFilterChange} />
        <h2>Add a new Number</h2>
        <PersonForm
          addPerson={addPerson}
          handleNameChange={handleNameChange}
          handleNumberChange={handleNumberChange}
          newName={newName}
          newNumber={newNumber}
        />
        <h2>Numbers</h2>
        <Persons
          persons={persons}
          handleDeleteClick={handleDeleteClick}
          newFilter={newFilter}
        />
      </Grid>
    </div>
  );
};

export default App;
