import React from "react";
import { Alert } from "@material-ui/lab";

const Notification = ({ message,status}) => {
  if (message === null) {
    return null;
  }

  return (
    <Alert variant="filled" severity={status === 'success' ? 'success' : 'error'}>
      {message}
    </Alert>
  );
};

export default Notification;
